const
	STR = 'string',
	FN = 'function',
	OBJ = 'object',
	NUM = 'number',
	BOOL = 'boolean',
	SYM = 'symbol',
	UNDEF = 'undefined',
	UCHAR = {
		copyright: '\u00a9',
		asterisk: '\u002a'
	},
	GT = '>',
	LT = '<',
	doc = document,
	body = doc.body

const util = {

	isFn( fn ) {
		return typeof fn === FN
	},
	
	nFn( fn ) {
		return !typeof fn === FN
	},

	isDef( val ) {
		return !(typeof val === UNDEF)
	},

	nDef( val ) {
		return typeof val === UNDEF
	},

	isStr( str ) {
		return typeof str === STR
	},
	
	nStr( str ) {
		return !typeof str === STR
	},

	isElement( el ) {
		return el instanceof HTMLElement
	},

	nElement( el ) {
		return !(el instanceof HTMLElement)
	},
	
	ready: (( queue ) => {

		doc.addEventListener('readystatechange', () => {
			for(let i = 0, len = queue.length; i < len; ++i ) {
				queue.pop()()
			}
		})

		return function( fn ) {
			if( typeof fn !== FN ) {
				throw new TypeError('argument not of function type')
			}

			doc.readyState === 'complete' ? fn() : queue.push(fn)
		}

	})([]),

	append( node, nodes = [] ) {
		for( let i = 0, len = nodes.length; i < len; ++i ) {
			node.appendChild(nodes[i])
		}
	},

	el( type ) {
		return doc.createElement(type)
	},
	
	txt( str ) {
		return doc.createTextNode(str)
	},

	node() {
		return doc.createDocumentFragment();
	},

	char( str ) {
		return UCHAR[str]
	},

	addCss(node, ...classes) {
		node.classList.add(...classes)
	},

	removeCss(node, ...classes) {
		node.classList.remove(...classes)
	},

	event(...args) {
		doc.addEventListener(...args)
	},

	cls( node, list_ ) {
		let list = typeof list_ === STR ? list_.split(' ') : list_

		for( let i = 0, len = list.length; i < len; ++i ) {
			node.classList.add(list[i])
		}

		return node
	},

	rect( el ) {
		return el.getBoundingClientRect()	
	},

	style( el ) {
		return window.getComputedStyle(el)
	}
}

const ops = {
	
	el( arg ) {
		if( arg === void 0 ) {
			return this._el
			
		} else if( typeof arg === STR ) {
			this._el = doc.createElement(arg)
			return this
		
		} else {
			throw new Error('Tag must be string type')
		}
	},
	
	cls( list_ ) {
		let list = typeof list_ === STR ? list_.split(' ') : list_

		for( let i = 0, len = list.length; i < len; ++i ) {
			this._el.classList.add(list[i])
		}

		return this
	}
	
}

export const _ = function(arg) {
	let ret

	if( typeof arg === STR ) {
		return Object.create(ops).el(arg)
	
	} else if( typeof arg === OBJ &&
		ops.isPrototypeOf(arg) ) {
		
		return arg.el()
	
	} else if( util.isElement(arg) ) {
		o = Object.create(ops)
		o._el = arg

		return o
	}
}

export default _

Object.assign(_, util)




